.img-hover-zoom {
    position: relative;
    margin: auto;
    overflow: hidden;
    }
    .img-hover-zoom img {
        max-width: 100%;
        transition: all 0.3s;
        display: block;
        width: 100%;
        height: auto;
        transform: scale(1);
      }
     
      /* [3] Finally, transforming the image when container gets hovered */
      .img-hover-zoom:hover img {
        transform: scale(1.1);
      }
    