@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;600&display=swap);
body {
  margin: 0;
  
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header_style{
    position: relative;
    text-align: center;
}
.centered {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: black;
  }
.text{
    color: #343434;
    color: var(--primary-color);
    opacity: 1!important;
}
:root{
    --primary-color: #343434;
}
.courses {
  display: flex;
  flex-direction: row;
  width: 100%;
}
.left_side {
 width: 100%;
  height: 50%;
}
.left_side nav {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 20px;
 
}
.courses li {
  list-style: none;
  margin: 10px;
}
.courses li a {
  display: block;
  color: white;
  border-radius: 5px;
  padding:5px 20px;
  /* padding: 5px; */
  font-size: 1.3rem;
  font-weight: bold;
  background-color: #02B3E4;
  text-decoration: none;
  transition: .3s;
  /* text-decoration: none;
  font-size: 1.5rem;
  color: black;
  transition: transform;
  padding: 10px; */
}

.courses li a:hover {
  color: white ;
  border-radius: 10px;
  background-color: #192F59;
}
.right_side {
}

@media screen and (max-width: 760px) {
   .left_side nav{
     flex-direction: column;
   }
 }
.img-hover-zoom {
    position: relative;
    margin: auto;
    overflow: hidden;
    }
    .img-hover-zoom img {
        max-width: 100%;
        transition: all 0.3s;
        display: block;
        width: 100%;
        height: auto;
        -webkit-transform: scale(1);
                transform: scale(1);
      }
     
      /* [3] Finally, transforming the image when container gets hovered */
      .img-hover-zoom:hover img {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
      }
    

.enroll_container {
  width: 100%;
  height: 100vh;
}
.enroll_style {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 100%;
}
.button-enroll {
  /* margin: 2rem 0; */
  font-size: 1.2rem;
  background-color: white;
  border: 2px solid black;
  border-radius: 10px;
  width: 100%;
  font-weight: bold;
  color: black;
  /* "to left" / "to right" - affects initial color */
  background: linear-gradient(to left, white 50%, #04aa6d 50%) right;
  background-size: 200%;
  transition: 0.5s ease-out;
}
.button-enroll:hover {
  background-position: left;
  color: white;
}

ul li {
   list-style: none;
  
}
ul li a{
    text-decoration: none;
    font-size: 1.5rem;
}
.design_login_button{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80vh;
}
.footer-container {
  position: relative;
}
.footer-container img {
  opacity: 0.9;
  width: 100%;
  height: 70vh;
}
.footer-container .content {
  position: absolute; /* Position the background text */
  background-color: rgba(14, 12, 44, 0.7);
 
  padding: 100px;
  text-align: center;
  font-weight: bold;
  color: #f1f1f1; /* Grey text */
  width: 100%;
  height: 70vh;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.footer-button {
  display: flex;
  justify-content: center;
}

/* CSS */
.button-66 {
  background-color: #0a6bff;
  border-radius: 4px;
  border: 0;
  box-shadow: rgba(1, 60, 136, 0.5) 0 -1px 3px 0 inset,
    rgba(0, 44, 97, 0.1) 0 3px 6px 0;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inherit;
  font-family: "Space Grotesk", -apple-system, system-ui, "Segoe UI", Roboto,
    Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
  margin: 10px;
  min-height: 56px;
  min-width: 120px;
  padding: 16px 20px;
  position: relative;
  text-align: center;
  -ms-user-select: none;
      user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  transition: all 0.2s cubic-bezier(0.22, 0.61, 0.36, 1);
}

.button-66:hover {
  background-color: white;
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
  color: #0a6bff;
}

@media (min-width: 768px) {
  .button-66 {
    padding: 16px 44px;
    min-width: 150px;
  }
}

